.hero {
    padding-top: 50px;
    padding-bottom: 130px;
    background: linear-gradient(99.31deg, #2C2D32 0%, #000A13 100%);
    @media (max-width: 768px) {
        padding-bottom: 55px; }

    &__container {
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: 1200px) {
            flex-direction: column; } }
    &__text {
        color: $white;
        max-width: 500px;
        @media (max-width: 1200px) {
            max-width: 442px;
            margin-bottom: 75px;
            text-align: center; } }


    &__heading {
        margin-bottom: 48px;
        font-size: 48px;
        line-height: 1.1;
        @media (max-width: 1200px) {
            font-size: 36px; }
        @media (max-width: 768px) {
            max-width: 260px;
            margin: 0 auto;
            margin-bottom: 26px;
            font-size: 24px;
            line-height: 1.6; } }
    &__logo {
        display: inline-block;
        max-width: 238px;
        max-height: 46px;
        @media (max-width: 768px) {
            max-width: 131px;
            max-height: 23px; }

        svg {
            object-fit: cover;
            max-width: 238px;
            max-height: 46px;
            @media (max-width: 768px) {
                max-width: 131px;
                max-height: 23px; } } }
    &__descr {
        font-size: 24px;
        margin-bottom: 50px;
        @media (max-width: 1200px) {
            font-size: 20px; } } }
