.nav-list {
    &__item {
        display: inline-block; }
    &__item:not(:last-of-type) {
        margin-right: 20px; }
    &__link {
        position: relative;
        text-transform: uppercase;
        font-size: 14px;
        @media (max-width: 952px) {
            font-size: 12px; } }
    @media (min-width: 1200px) {
        &__link::before {
            content: '';
            position: absolute;
            left: auto;
            right: 0;
            bottom: 0;
            width: 0;
            height: 1px;
            background-color: $emerald;
            transition: width .4s; } }

    &__link:hover {
        &::before {
            width: 100%;
            right: auto;
            left: 0; } }
    &__link.nav-list__link--current {
        &::before {
            width: 100%;
            right: auto;
            left: 0; } } }



.btn-secondary {
    padding: 10px 13px;
    width: 140px;
    font-size: 14px;
    color: $white;
    border: 1px solid $white;
    border-radius: 5px; }

.callback {
    display: flex;
    align-items: center; }
.callback__whatsapp {
    position: relative;
    display: inline-block;

    width: 32px;
    height: 32px;
    background-color: $white;
    border-radius: 100%;


    svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;
        max-width: 17px;
        max-height: 17px;
        margin: 0 auto; } }

.btn-primary {
    padding: 25px 50px;
    font-size: 14px;
    text-transform: uppercase;
    color: $white;
    background-color: $emerald;
    border-radius: 5px;
    transition: filter .3s ease;
    cursor: pointer;


    @media (min-width: 1200px) {
        &:hover {
            filter: drop-shadow(-5px -5px 10px rgba(51, 204, 102, 0.2)) drop-shadow(5px 5px 10px rgba(51, 204, 102, 0.2)); } }
    @media (max-width: 1200px) {
        &:active {
           background-color: $white;
           color: $emerald;
           border: 1px solid $emerald; } } }


.burger {
    display: none;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 34px;
    height: 24px;
    @media (max-width: 768px) {
        display: flex; }
    &__line {
        width: 34px;
        height: 2px;
        background-color: $white;
        border-radius: 5px; } }
.burger::before, .burger::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 34px;
    height: 2px;
    background-color: $white;
    border-radius: 5px;
    opacity: 0;
    pointer-events: none; }
.burger::before {
    transform: translate(-50%, -50%) rotate(-45deg); }
.burger::after {
    transform: translate(-50%, -50%) rotate(45deg); }

